@import "src/styles/variables";

.container {
  width: 25%;
  height: 300px;
  position: relative;
  margin-bottom: 32px;
  cursor: pointer;

  &:hover {
    .bgWrapper {
      transform: scale(1.1);
      border-radius: 10px;
    }

    .bg {
      transform: scale(1);
      filter: grayscale(0);
    }

    .title {
      left: 0;
      right: 0;
      letter-spacing: 1px;
    }
  }
}

.bgWrapper {
  position: absolute;
  top: 0;
  left: 16px;
  right: 16px;
  bottom: 0;
  overflow: hidden;
  transform: scale(1);
  border-radius: 0;
  transition: 0.3s transform;
}

.bg {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transform: scale(1.2);
  filter: grayscale(0.8);
  transition: 0.3s transform, 0.3s filter;
}

.title {
  font-size: 24px;
  color: $white;
  position: absolute;
  left: 16px;
  right: 16px;
  bottom: 30px;
  text-align: center;
  padding: 16px 0;
  transition: 0.3s left, 0.3s right, 0.3s letter-spacing;
  background-color: $glass;
}

@media screen and (max-width: 1300px) {
  .container {
    height: 200px;
  }
}

@media screen and (max-width: 1100px) {
  .container {
    width: 50%;
    height: 250px;
  }

  .title {
    font-size: 18px;
  }
}

@media screen and (max-width: 800px) {
  .container {
    width: 100%;
  }

  .title {
    font-size: 16px;
  }
}

@media screen and (max-width: 500px) {
  .container {
    height: 200px;
  }

  .title {
    font-size: 16px;
  }
}