@import "src/styles/variables";

.wrapper {
  position: relative;
  background-image: linear-gradient(to right, #0b0425, #160c52, #1d1193, #1a0e62, #0d0324);
  overflow: hidden;

  &::after {
    content: '';
    display: inline-block;
    width: calc(100% + 40px);
    transform: translateX(-20px);
    height: 40px;
    position: absolute;
    top: 0;
    box-shadow: inset 0 20px 20px #11083c;
  }
}

.container {
  padding: 52px 32px 32px;
}

.wrap {
  padding: 16px;
  margin-top: 32px;
  border-radius: 10px;
  margin-bottom: 16px;
  @include border;
  @include shadow;
}

.boxWrapper {
  margin-right: -8px;
}

.skills {
  display: flex;
  flex-wrap: wrap;
}

.boxTitle {
  color: $white;
  font-size: 24px;
  margin-bottom: 32px;
}

.skillItem {
  padding: 16px;
  border-radius: 8px;
  margin-right: 8px;
  margin-bottom: 8px;
  position: relative;
  overflow: hidden;
  background-color: $white;
  @include border;

  &:hover {
    .progressWrapper {
      width: 100%
    }
  }
}

.titleWrapper {
  display: inline-flex;
  align-items: center;
  font-size: 18px;
  color: $gray4;
  font-weight: 600;
  position: relative;
  z-index: 1;

}

.title {
  margin-left: 8px;
}

.progressWrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  border-radius: 8px;
  transition: 0.3s width;
}

.progress {
  width: 100%;
  height: 100%;
  border-radius: 8px;
}


@media screen and (max-width: 1000px) {
  .skillItem {
    flex-grow: 1;
    text-align: center;
  }
}

@media screen and (max-width: 500px) {
  .container {
    padding: 52px 8px 8px;
  }
}