@import "src/styles/variables";

.container {
  overflow: hidden;
}

.listItem {
  display: flex;
  padding: 8px;
  border-radius: 10px;
  margin-bottom: 16px;
  @include border;
  @include shadow;
}

.leftSide {
  width: 250px;
  flex-shrink: 0
}

.rightSide {
}

.projects {
}

.title {
  color: $white;
  margin-bottom: 8px;
  font-size: 18px;
}

.date {
  color: $blue1;
}

.list {
  display: flex;
  flex-direction: column;
}

.project {
  font-size: 14px;
  color: $white;
  margin-bottom: 4px;
  transition: 0.3s transform;
}

.hasLink {
  cursor: pointer;
  display: inline-block;
  text-decoration: underline;

  &:hover {
    color: $blue1;
  }
}


@media screen and (max-width: 800px) {
  .leftSide {
    width: 185px;
  }
}

@media screen and (max-width: 768px) {
  .leftSide {
    width: 250px;
  }
}

@media screen and (max-width: 600px) {
  .listItem {
    flex-direction: column;
  }

  .leftSide {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .title {
    flex-grow: 1;
    padding-right: 16px;
  }
}

@media screen and (max-width: 500px) {
  .leftSide {
    flex-direction: column;
    margin-bottom: 16px;
  }
}