@import "src/styles/variables";

.wrapper {
  height: 90vh;
  min-height: 700px;
  max-height: 1200px;
  overflow: hidden;
  position: relative;
}

.container {
  height: 100%;
  background-image: url("../../assets/images/bg.jpg");
  background-position: center;
  background-size: cover;
  padding: 200px 32px 32px;
  transform-style: preserve-3d;
}

.textWrapper {
  width: 400px;
  display: inline-block;
  padding: 23px;
  border-radius: 10px;
  flex-shrink: 0;
  overflow: hidden;
  @include glass;
  @include shadow;
}

.text {
  font-size: 60px;
  color: $white;
  display: block;
  opacity: 0;
  transform: translateY(-100px);
}


@media screen and (max-width: 768px) {
  .container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 200px 0;
  }

  .text {
    font-size: 40px;
    text-align: center;
  }

  .textWrapper {
    width: 100%;
  }
}


