$white: #f5f5f7;
$extraWhite: #ffffff;
$purple1: #60058c;
$purple2: #570f8d;
$purple3: #3c0b8c;
$blue1: #1aaccc;
$blue2: #4812e4;
$blue3: #0f137c;
$gray1: #585858;
$gray2: #333333;
$gray3: #292929;
$gray4: #1f1f1f;
$black: #000000;
$glass: rgba(23, 12, 61, 0.2);

@mixin gradient1 {
  background-image: linear-gradient($purple1, $purple2);
}

@mixin gradient2 {
  background-image: linear-gradient($blue2, $blue3);
}

@mixin gradient3 {
  background-image: linear-gradient($blue1, $purple3);
}

@mixin shadow {
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2)
}

@mixin border {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

@mixin glass {
  background: $glass;
  backdrop-filter: blur(10px);
}

// Intro ******
@mixin intro($delay) {
  animation-name: intro;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: $delay;
  opacity: 0;
  transform: translateY(-50px);
}

@keyframes intro {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  60% {
    transform: translateY(3px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// Intro ******

// Blink ******
@mixin blink($duration, $delay) {
  position: relative;

  &:after {
    content: '';
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    animation-name: blink;
    animation-duration: $duration;
    animation-delay: $delay;
    animation-iteration-count: infinite;
    background-size: 200%;
    background-repeat: no-repeat;
    background-position: 150%;
    animation-timing-function: cubic-bezier(0.34, 0.65, 0.17, 1);
    background-image: linear-gradient(-85deg, transparent 48%, rgba(255, 255, 255, 0.1) 50%, transparent 52%);
    pointer-events: none;
  }
}

@keyframes blink {
  from {
    background-position: 150%;
    opacity: 1;
  }
  to {
    background-position: -50%;
    opacity: 0;
  }
}

// Blink ******

// Float ******
@mixin float($delay) {
  animation: float 6s ease-in-out infinite;
  animation-delay: $delay;
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

// Float ******
