@import "src/styles/variables";

.textWrapper {
  display: inline-flex;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  color: $white;
  padding: 16px 32px;
  border-radius: 10px;
  @include glass;
  @include shadow;
  @include border;
}

.text {
  margin-left: 16px;
}

@media screen and (max-width: 500px) {
  .textWrapper {
    font-size: 20px;
  }
}