@import "src/styles/variables";

.wrapper {
  position: relative;
  background-image: linear-gradient(to right, #0b0425, #160c52, #1d1193, #1a0e62, #0d0324);
  overflow: hidden;

  &::after {
    content: '';
    display: inline-block;
    width: calc(100% + 40px);
    transform: translateX(-20px);
    height: 40px;
    position: absolute;
    top: 0;
    box-shadow: inset 0 20px 20px #11083c;
  }
}

.container {
  padding: 52px 32px 32px;
}

.projectsWrapper {
  margin-top: 32px;
  background-color: $glass;
  padding: 64px 64px 32px;
  border-radius: 10px;
  @include border
}

.projects {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 1300px) {
  .projectsWrapper{
    padding: 32px 32px 0;
  }
}

@media screen and (max-width: 500px) {
  .container {
    padding: 52px 8px 8px;
  }
}